@import '../../constants/vars';

.listItem:first-of-type {
  margin-top: 0;
}

.listItem {
  color: white;
  background-color: #333333;
  margin-top: $base-padding/2;
  padding: $base-padding;

  cursor: pointer;

  .disabled {
  background-color: grey;
}

.approved {
  background-color: green;
}

}

.exitButton {
  font-size: 2em;
  color: $placeholder-color;
  cursor: pointer;
  min-height: 1em;
}

.listItem:hover {
  background-color: #222222;
}

.statusLabel {
  margin: 3px;
  padding: 3px;
  font-size: 0.7em;
}

.deniedLabel {
  background-color: red;
  font-size: 0.7em;
  padding: 3px;
  margin: 3px;
}

